import { Component} from '@angular/core';
import {IonicPage, NavController, NavParams, LoadingController, ToastController} from 'ionic-angular';
import * as firebase from 'firebase';

@IonicPage({
  'name': 'GroupPage'
})
@Component({
  selector: 'page-group',
  templateUrl:'group.html'
})
export class GroupPage {

  lctr;
  user = firebase.auth().currentUser;
  userGroupIds:Array<string> = [];
  userGroupsData:Array<Object> = [];
  isGroupsEmpty:boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, public loadCtrl: LoadingController, public toastCtrl: ToastController){
    this.lctr = this.loadCtrl.create({
      content:'Please Wait...'
    });
    this.lctr.present();
  }

  ionViewDidLoad() {
    this.userGroupIds = [];
    this.userGroupsData = [];
    this.getUserGroupData((result)=>{
      if(result != undefined && result.length != 0){
        this.userGroupsData = result;
      }else{
        this.isGroupsEmpty = true;
      }
    });
  }

  async getUserGroupData(callback) {
    let groupsData:Array<Object> = [];
    if(this.user) {
      await firebase.database().ref('userDetails').orderByChild('mobileNo').equalTo(this.user.phoneNumber).on('child_added', (datasnapshot)=>{
        if(datasnapshot.val().group_id != undefined && datasnapshot.val().group_id !== '') {
          let gids = datasnapshot.val().group_id;
          if(gids == undefined || gids == '') {
            this.lctr.dismiss();
          }
          for(let i = 0; i<gids.length; i++){
            if(gids[i] != undefined && gids[i] != ''){
                firebase.database().ref('groups').orderByKey().equalTo(gids[i]).on('child_added', (datasnapshot)=>{
                if(datasnapshot.val().is_active){
                  let obj = datasnapshot.val();
                  obj['groupid'] = datasnapshot.key;
                  if(datasnapshot.val().followers != undefined && datasnapshot.val().followers != ''){
                    groupsData.unshift(obj);
                  }else{
                    obj['followers'] = 0;
                    groupsData.unshift(obj);
                  }
                  callback(groupsData);
                }  
              });
            }
          }
          this.lctr.dismiss();
        }else {
          this.lctr.dismiss();
        }
      });
    }else{
      this.lctr.dismiss();
      let tctrl = this.toastCtrl.create({
        message:"Your are not logged in."
      });
      tctrl.present();
    }
  }

  doRefresh(event){
    this.userGroupIds = [];
    this.userGroupsData = [];
    this.getUserGroupData((result)=>{
      if(result != undefined && result.length != 0){
        this.userGroupsData = result;
      }
    });
    setTimeout(()=>{ event.complete();}, 1000);
  }

  openGroup(index) {
    let group:Object = this.userGroupsData[index];
    this.navCtrl.push("DetailViewPage", {'data':group});
  }
  
}


